/* eslint-disable */ 
import axios from "axios";
import React, { useState } from "react"; 
import { useNavigate } from "react-router-dom";

import 'bootstrap/dist/css/bootstrap.min.css'


function VisitForm(){ 

    const [visit,setVisit] = useState({name:"",content:"",pwd:""});
       
    const navigate = useNavigate();

    function updateData(e){
        let value = e.target.value;
        let id    = e.target.id;
        setVisit({...visit,[id]:value});
    }

    function addVisit(){

        if(visit.name==''){
            alert(' !.');
            return;
        }
        if(visit.content==''){
            alert(' !.');
            return;
        }
        if(visit.pwd==''){
            alert(' !.');
            return;
        }
       // axios.defaults.withCredentials = true;
        axios.post('https://ycm111.shop/demo_visit/rest/visit',visit)
        .then((res)=>{

          // 
          setVisit({name:"",content:"",pwd:""});
          
          navigate('/');

        })
        .catch((error)=>{
          console.log(error);
        });  
    }

    
    return ( 
           <div>
               <div style={{width:"500px",margin:"auto",padding:"10px"}}>
                    <form className="form-inline">
                        <h2 style={{ color:'#999',fontSize:'20px'}}>   </h2> 
                        <br/>
                        <input className="form-control" id="name" value={visit.name} onChange={updateData} placeholder=" " />
                        <br/>
                        <textarea className="form-control" style={ {width:'100%',resize:'none'} } id="content" rows={5} cols={50} onChange={updateData} value={visit.content} placeholder=" "></textarea>
                        <br/>
                        <input className="form-control" type="password" id="pwd" value={visit.pwd} onChange={updateData} placeholder=" "/>
                        <br/>
                        <input className="btn btn-success"  type="button" value="" onClick={ addVisit }/>
                    </form>    
                </div>

            </div>

    ); 
} 
export default VisitForm;